import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { authApi } from "../../../utils/authApi";
import { pattern } from "../../../utils/validations";
import { BaseButton } from "../../BaseButton/BaseButton";
import { Form } from "../../Form/Form";
import "../auth_style.css";
import CloseIcon from '@mui/icons-material/Close';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export const LogIn = ({ setShowModal }) => {
  const [type, setType] = useState(false);
  
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: "onSubmit" });

  const navigate = useNavigate();

  const handleClick = (e) => {
    e.preventDefault();
    navigate("/register");
  };

 
  const sendData = async (data) => {
    try {
      const res = await authApi.login(data);
      localStorage.setItem("token", res.token);
      navigate("/");
    } catch (error) {
      console.log();
      alert('  ');
    }
  };

  const emailRegister = register("email", {
    required: "E-mail ",
  });
  const passwordRegister = register("password", {
    
    required: " ",
    pattern
  });

  
  
  useEffect(() => {
    setShowModal(true);
  }, [setShowModal]);

 
 
 
  return (
    <>
      <div className="form_close_butn_wrapper">
      <button className="form_close_btn" title="" onClick={()=>{setShowModal(false); navigate('/not_authenticated')}}><CloseIcon/></button>
      </div>
      <Form submitForm={handleSubmit(sendData)} title={""}>
        <div className="auth__controls">
          <input
            type="text"
            {...emailRegister}
            placeholder="Email"
            className="auth__input"
          />
          {errors?.email && (
            <span className="auth__info">{errors.email?.message}</span>
          )}
          <input
            type={type ? "text" : "password"}
            {...passwordRegister}
            placeholder=""
            className="auth__input"
          />
          {errors?.password && (
            <span className="auth__info">{errors.password?.message}</span>
          )}
          {!errors?.password &&
                     <span className="auth__eye_register" onClick={() => setType(!type)}>
                        {type ? <VisibilityOffIcon/> : <VisibilityIcon/>}
                    </span>
                    
                    }

          <span className="auth__info auth__link" onClick={() => navigate('/reset-password')}> </span>
          
          
          <div className="auth__actions">
            <BaseButton type="submit" color={"yellow"}>
              <span></span>
            </BaseButton>
            <BaseButton onClick={handleClick} color={"white"}>
              <span></span>
            </BaseButton>
          </div>
        </div>
      </Form>
    </>
  );
};